import React, { useCallback, useEffect, useState } from "react";
import PhoneInput from "react-phone-input-2";
import { Button } from "@mui/material";

import useLocalStorage from "../../../../hooks/useLocalStorage";
import GetStartedPopUp from "../../components/GetStartedPopUp";

const Hero = ({ className }) => {
  const [country, setCountry] = useLocalStorage("country", null),
    [phoneNo, setPhoneNo] = useState(""),
    [getStarted, triggerGetStarted] = useState(false),
    openGetStarted = () => triggerGetStarted(true),
    closeGetStarted = (event, reason) => {
      triggerGetStarted(false);
    },
    getCountry = useCallback(async () => {
      try {
        const visitorReq = await fetch("//api.ipregistry.co/?key=tryout", {
          method: "GET",
        });
        if (visitorReq.ok) {
          const visitor = await visitorReq.json();
          setCountry(visitor?.location?.country?.code.toLowerCase());
        } else {
          setCountry("in");
        }
      } catch (error) {
        console.error(error);
      }
    }, [setCountry]),
    handleSubmit = (event) => {
      event.preventDefault();
      openGetStarted();
    };

  useEffect(() => {
    country === null && getCountry();
  }, [country, getCountry]);

  return (
    <section className={className}>
      <div className="content">
        <h1 className="heading">
          {"Grow Faster with WhatsApp Chatbot & Social Media Automation"}
        </h1>
        {country && (
          <form className="signup" onSubmit={handleSubmit}>
            <PhoneInput
              country={country}
              value={phoneNo}
              onChange={(phone) => setPhoneNo(phone)}
              // enableAreaCodes={true}
              placeholder="Enter your phone number"
              containerClass="phone-input"
              inputProps={{
                name: "phone",
                required: true,
              }}
            />
            <Button variant="contained" type="submit">
              {"Sign Up Free"}
            </Button>
          </form>
        )}
        <ul className="features">
          <li>{"Free tools with a lot of features"}</li>
          <li>{"No credit card required"}</li>
        </ul>
      </div>
      <GetStartedPopUp
        open={getStarted}
        onClose={closeGetStarted}
        phone={phoneNo}
      />
    </section>
  );
};

export default Hero;
