import React, { useCallback, useEffect, useState } from "react";
// axios
import axios from "axios";

import PhoneInput from "react-phone-input-2";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormControl,
  Select,
  MenuItem,
  OutlinedInput,
} from "@mui/material";

// backend url
import { API } from "../../../../Backend";

import { useToastMessage } from "../../../../contexts/ToastMessageContext";

import useLocalStorage from "../../../../hooks/useLocalStorage";
import useStyles from "./styles/muiStyles";

const GetStartedPopUp = ({ open, onClose, plan = "", phone = "" }) => {
  const { setErrorMessage } = useToastMessage();
  const classes = useStyles(),
    [country, setCountry] = useLocalStorage("country", null),
    [phoneNo, setPhoneNo] = useState(phone),
    [selectedPlan, setSelectedPlan] = useState(plan),
    [selectedIndustry, setSelectedIndustry] = useState(""),
    [, setFullName] = useState(""),
    [, setCompanyName] = useState(""),
    [, setDesignation] = useState(""),
    [submitted, setSubmitted] = useState(false),
    getCountry = useCallback(async () => {
      try {
        const visitorReq = await fetch("//api.ipregistry.co/?key=tryout", {
          method: "GET",
        });
        if (visitorReq.ok) {
          const visitor = await visitorReq.json();
          setCountry(visitor?.location?.country?.code.toLowerCase());
        } else {
          setCountry("in");
        }
      } catch (error) {
        console.error(error);
      }
    }, [setCountry]),
    handleSubmit = async (event) => {
      try {
        event.preventDefault();
        const url = `${API}/business-enquiry`,
          {
            target: fromElement,
            target: {
              elements: {
                fullName: { value: fullName },
                companyName: { value: companyName },
                industryType: { value: industryType },
                designation: { value: designation },
                plan: { value: plan },
                phone: { value: phone },
              },
            },
          } = event,
          result = await axios.post(url, {
            name: fullName,
            company: companyName,
            industry: industryType,
            designation,
            plan,
            contactNo: phone,
            subject: "EasySocial Enquiry - New User",
          });

        if (result.data.success) {
          setSubmitted(true);
          setSelectedIndustry("");
          setPhoneNo("");
          setSelectedPlan("");
          fromElement.reset();
        }
      } catch (err) {
        setErrorMessage(err.message);
        // console.error(err.message);
      }
    };

  useEffect(() => {
    country === null && getCountry();
    phone !== "" && setPhoneNo(phone);
  }, [country, getCountry, phone, setPhoneNo]);

  return (
    <Dialog
      open={open}
      onClose={onClose}
      maxWidth="xs"
      className={classes.popup}
    >
      <form onSubmit={handleSubmit}>
        {submitted && (
          <div className="form-submitted">
            <DialogTitle>{"Thank You!"}</DialogTitle>
            <DialogContent>
              <h4>
                {
                  "We have registered your interest. We will get in touch with you shortly."
                }
              </h4>
              <Button
                variant="contained"
                type="button"
                onClick={() => {
                  setSubmitted(false);
                  onClose();
                }}
              >
                {"Close"}
              </Button>
            </DialogContent>
          </div>
        )}
        <DialogTitle>{"Share your details to begin."}</DialogTitle>
        <DialogContent>
          <div className="field-group">
            <svg
              width="17"
              height="18"
              viewBox="0 0 17 18"
              fill="none"
              xmlns="//www.w3.org/2000/svg"
            >
              <g opacity="0.28">
                <path
                  d="M11.4366 1.30901C10.6547 0.464863 9.56268 0 8.35733 0C7.14555 0 6.04989 0.462051 5.27163 1.30097C4.48494 2.14914 4.10164 3.30186 4.19164 4.54658C4.37003 7.00228 6.23872 8.99995 8.35733 8.99995C10.4759 8.99995 12.3414 7.00268 12.5226 4.54738C12.6138 3.31391 12.2281 2.1636 11.4366 1.30901Z"
                  fill="#263238"
                />
                <path
                  d="M15.4287 17.9999H1.28594C1.10082 18.0023 0.917496 17.9634 0.7493 17.886C0.581105 17.8087 0.432269 17.6948 0.313621 17.5527C0.0524616 17.2405 -0.0528056 16.8142 0.0251404 16.3831C0.364246 14.502 1.42254 12.9217 3.08593 11.8124C4.56369 10.8277 6.43559 10.2856 8.35732 10.2856C10.2791 10.2856 12.151 10.8281 13.6287 11.8124C15.2921 12.9213 16.3504 14.5016 16.6895 16.3827C16.7675 16.8138 16.6622 17.2401 16.401 17.5523C16.2824 17.6945 16.1336 17.8084 15.9654 17.8859C15.7972 17.9633 15.6139 18.0022 15.4287 17.9999Z"
                  fill="#263238"
                />
              </g>
            </svg>
            <input
              type="text"
              name="fullName"
              placeholder="Full Name"
              onChange={({ target: { value } }) => {
                setFullName(value);
              }}
            />
          </div>
          <div className="field-group">
            <svg
              width="18"
              height="17"
              viewBox="0 0 18 17"
              fill="none"
              xmlns="//www.w3.org/2000/svg"
            >
              <path
                opacity="0.28"
                d="M17.1 3.6H11.7C11.4613 3.6 11.2324 3.69482 11.0636 3.8636C10.8948 4.03239 10.8 4.26131 10.8 4.5V7.2H9V0.9C9 0.661305 8.90518 0.432387 8.7364 0.263604C8.56761 0.0948211 8.33869 0 8.1 0H0.9C0.661305 0 0.432387 0.0948211 0.263604 0.263604C0.0948211 0.432387 0 0.661305 0 0.9V15.3C0 15.5387 0.0948211 15.7676 0.263604 15.9364C0.432387 16.1052 0.661305 16.2 0.9 16.2H17.1C17.3387 16.2 17.5676 16.1052 17.7364 15.9364C17.9052 15.7676 18 15.5387 18 15.3V4.5C18 4.26131 17.9052 4.03239 17.7364 3.8636C17.5676 3.69482 17.3387 3.6 17.1 3.6ZM5.4 2.7H7.2V4.5H5.4V2.7ZM3.6 11.7H1.8V9.9H3.6V11.7ZM3.6 8.1H1.8V6.3H3.6V8.1ZM3.6 4.5H1.8V2.7H3.6V4.5ZM7.2 11.7H5.4V9.9H7.2V11.7ZM7.2 8.1H5.4V6.3H7.2V8.1ZM15.3 11.7H13.5V9.9H15.3V11.7ZM15.3 8.1H13.5V6.3H15.3V8.1Z"
                fill="#263238"
              />
            </svg>
            <input
              type="text"
              name="companyName"
              placeholder="Company Name"
              onChange={({ target: { value } }) => {
                setCompanyName(value);
              }}
            />
          </div>
          <div className="field-group">
            <svg
              width="18"
              height="18"
              viewBox="0 0 18 18"
              fill="none"
              xmlns="//www.w3.org/2000/svg"
            >
              <g opacity="0.28">
                <path
                  d="M12.5217 16.8261H10.7608V1.76087C10.7608 0.789889 9.97092 0 8.99994 0C8.02896 0 7.23907 0.789889 7.23907 1.76087V16.8261H5.4782C5.15396 16.8261 4.89124 17.0888 4.89124 17.413C4.89124 17.7373 5.15396 18 5.4782 18C8.20664 18 10.2395 18 12.5217 18C12.8459 18 13.1086 17.7373 13.1086 17.413C13.1086 17.0888 12.8459 16.8261 12.5217 16.8261Z"
                  fill="#263238"
                />
                <path
                  d="M17.828 7.80235L16.0671 6.04152C15.957 5.93145 15.8078 5.86954 15.6521 5.86954H11.9347V10.5652H15.6521C15.8078 10.5652 15.957 10.5033 16.0671 10.3932L17.828 8.63232C18.0572 8.40328 18.0572 8.03147 17.828 7.80235Z"
                  fill="#263238"
                />
                <path
                  d="M2.34779 9.39127C2.19208 9.39127 2.04285 9.45318 1.93281 9.56326L0.17194 11.3241C-0.0573134 11.5532 -0.0573134 11.925 0.17194 12.1541L1.93281 13.915C2.04285 14.025 2.19208 14.0869 2.34779 14.0869H6.06517V9.39127H2.34779Z"
                  fill="#263238"
                />
                <path
                  d="M2.34779 1.1739C2.19208 1.1739 2.04285 1.23581 1.93281 1.34588L0.17194 3.10675C-0.0573134 3.33583 -0.0573134 3.70764 0.17194 3.93675L1.93281 5.69759C2.04285 5.80766 2.19208 5.86957 2.34779 5.86957H6.06517V1.76087C6.06517 1.55988 6.08567 1.3636 6.12434 1.1739H2.34779Z"
                  fill="#263238"
                />
              </g>
            </svg>
            <FormControl>
              <Select
                displayEmpty
                input={<OutlinedInput />}
                inputProps={{ "aria-label": "Without label" }}
                name="industryType"
                value={selectedIndustry}
                onChange={({ target: { value } }) => {
                  setSelectedIndustry(value);
                }}
              >
                <MenuItem disabled value="">
                  <em>Select Industry</em>
                </MenuItem>
                {[
                  "Automotive",
                  "Beauty, Spa and Salon",
                  "Clothing and Apparel",
                  "Education",
                  "Entertainment",
                  "Event Planning and Service",
                  "Finance and Banking",
                  "Food and Grocery",
                  "Public Service",
                  "Hotel and Lodging",
                  "Medical and Health",
                  "Non-profit",
                  "Professional Services",
                  "Shopping and Retail",
                  "Travel and Transportation",
                  "Restaurant",
                  "Other",
                ].map((industry, index) => (
                  <MenuItem key={index} value={industry}>
                    {industry}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </div>
          <div className="field-group">
            <svg
              width="18"
              height="14"
              viewBox="0 0 18 14"
              fill="none"
              xmlns="//www.w3.org/2000/svg"
            >
              <path
                d="M11.5279 12.0926C11.5279 10.4884 10.2274 9.18788 8.62313 9.18788H7.87331C7.48406 9.18788 7.1685 8.87231 7.1685 8.48306C7.1685 8.3385 7.227 8.20012 7.32994 8.09888C7.76587 7.67362 8.13263 7.10325 8.39812 6.489C8.45213 6.52894 8.51006 6.55762 8.57531 6.55762C8.99662 6.55762 9.49106 5.62725 9.49106 4.99275C9.49106 4.35825 9.43256 3.84412 9.01069 3.84412C8.96119 3.84412 8.90775 3.85256 8.85431 3.86606C8.8245 2.14594 8.38969 0 5.76338 0C3.02288 0 2.70281 2.142 2.67244 3.86044C2.63419 3.85312 2.59481 3.84469 2.55938 3.84469C2.13806 3.84469 2.07956 4.35881 2.07956 4.99331C2.07956 5.62781 2.574 6.55819 2.99531 6.55819C3.04706 6.55819 3.09544 6.54356 3.14044 6.51769C3.40481 7.12238 3.76706 7.68206 4.19681 8.1C4.30031 8.20125 4.35825 8.33962 4.35825 8.48419C4.35825 8.87344 4.04269 9.189 3.65344 9.189H2.90475C1.3005 9.18787 0 10.4884 0 12.0926V12.8981C0 13.4044 0.410625 13.8156 0.917438 13.8156H10.6104C11.1178 13.8156 11.5284 13.4049 11.5284 12.8981L11.5279 12.0926Z"
                fill="#263238"
                opacity="0.28"
              />
              <path
                d="M9.19125 1.21838C9.42582 1.73756 9.59344 2.37656 9.66375 3.16744C9.83757 3.27431 9.96694 3.44756 10.0738 3.65625H17.3205C17.6957 3.65625 18 3.35194 18 2.97675V1.89844C18 1.52325 17.6957 1.21894 17.3205 1.21894L9.19125 1.21838Z"
                fill="#263238"
                opacity="0.28"
              />
              <path
                d="M8.91954 7.3485C8.90042 7.38394 8.87735 7.41544 8.85767 7.45144V8.09662H17.3205C17.6957 8.09662 18 7.79231 18 7.41769V6.33937C18 5.96419 17.6957 5.65987 17.3205 5.65987H10.2397C10.0479 6.38606 9.57373 7.14544 8.91954 7.3485Z"
                fill="#263238"
                opacity="0.28"
              />
              <path
                d="M12.3716 12.0926V12.5392H17.3205C17.6957 12.5392 18 12.2349 18 11.8597V10.7814C18 10.4062 17.6957 10.1019 17.3205 10.1019H11.7939C12.1573 10.6785 12.3716 11.3597 12.3716 12.0926Z"
                fill="#263238"
                opacity="0.28"
              />
            </svg>
            <input
              type="text"
              name="designation"
              placeholder="Designation"
              onChange={({ target: { value } }) => {
                setDesignation(value);
              }}
            />
          </div>
          {country && (
            <div className="field-group">
              <svg
                width="16"
                height="18"
                viewBox="0 0 16 18"
                fill="none"
                xmlns="//www.w3.org/2000/svg"
              >
                <path
                  opacity="0.28"
                  d="M5.17066 8.89483C4.57675 8.02992 4.22345 6.98635 4.15055 6.8977C5.02039 6.28749 6.72163 5.52068 6.81688 4.42041C6.93808 2.99884 4.54455 0.0270004 4.0857 0C2.8299 0.0135002 0.53163 2.15193 0.259272 2.79814C-0.246983 4.13421 0.00390859 5.37758 0.874202 7.1776C1.98242 9.85514 6.09865 14.9344 8.45327 16.5287C10.0114 17.7321 11.1496 18.2046 12.5087 17.9193C13.1406 17.7987 15.4228 15.9145 15.6513 14.6405C15.7072 14.168 13.0543 11.0355 11.6822 10.9113C10.6169 10.8123 9.59547 12.417 8.86337 13.2014C8.79047 13.1078 8.16615 12.6177 7.49621 11.8419C6.62413 10.9199 5.81377 9.83669 5.17066 8.89483Z"
                  fill="#263238"
                />
              </svg>
              <PhoneInput
                country={country}
                value={phoneNo}
                onChange={(phone) => setPhoneNo(phone)}
                // enableAreaCodes={true}
                placeholder="Enter your phone number"
                containerClass="phone-input"
                inputProps={{
                  name: "phone",
                  required: true,
                }}
              />
            </div>
          )}
          <div className="field-group">
            <svg
              width="18"
              height="13"
              viewBox="0 0 18 13"
              fill="none"
              xmlns="//www.w3.org/2000/svg"
            >
              <path
                d="M17.9995 5.38237L18 10.1404C18 11.4581 16.9714 12.5356 15.6733 12.6136L15.5223 12.6181H2.47831C1.16061 12.6181 0.0831733 11.5895 0.00517041 10.2914L0.000648502 10.1404L0 5.38237H17.9995ZM14.634 8.54052H12.3816L12.2899 8.54669C11.9601 8.59143 11.7059 8.87415 11.7059 9.21624C11.7059 9.55834 11.9601 9.84106 12.2899 9.8858L12.3816 9.89197H14.634L14.7257 9.8858C15.0555 9.84106 15.3097 9.55834 15.3097 9.21624C15.3097 8.87415 15.0555 8.59143 14.7257 8.54669L14.634 8.54052ZM15.5223 0C16.84 0 17.9175 1.02864 17.9955 2.32672L18 2.47766L17.9995 4.03092H0L0.000648502 2.47766C0.000648502 1.15997 1.02929 0.0825246 2.32737 0.00452169L2.47831 0H15.5223Z"
                fill="#263238"
                opacity="0.28"
              />
            </svg>
            <FormControl>
              <Select
                displayEmpty
                input={<OutlinedInput />}
                inputProps={{ "aria-label": "Without label" }}
                name="plan"
                value={selectedPlan}
                onChange={({ target: { value } }) => {
                  setSelectedPlan(value);
                }}
              >
                <MenuItem disabled value="">
                  <em>Select Plan</em>
                </MenuItem>
                {[
                  { label: "WhatsApp Basic", value: "a" },
                  { label: "WhatsApp Smart", value: "b" },
                  { label: "WhatsApp Professional", value: "c" },
                  { label: "WhatsApp Enterprise", value: "d" },
                ].map(({ label, value }, index) => (
                  <MenuItem key={index} value={value}>
                    {label}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </div>
        </DialogContent>
        <DialogActions>
          <Button variant="contained" type="submit">
            {"Lets get Started"}
            <svg
              width="14"
              height="10"
              viewBox="0 0 14 10"
              fill="none"
              xmlns="//www.w3.org/2000/svg"
            >
              <path
                fillRule="evenodd"
                clipRule="evenodd"
                d="M11.3692 4.19223L8.28352 1.00205L9.25275 0L14 4.90798L9.43077 10L8.46154 8.99796L11.3692 5.62372H0V4.19223H11.3692Z"
                fill="white"
              />
            </svg>
          </Button>
        </DialogActions>
      </form>
    </Dialog>
  );
};

export default GetStartedPopUp;
