import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  popup: {
    "& .MuiDialog-paper": {
      backgroundColor: "#FFFFFF",
      border: "1px solid rgba(0, 0, 0, 0.1)",
      boxShadow: "0px 5px 40px rgba(18, 176, 238, 0.1)",
      borderRadius: "12px",

      "& form": {
        color: "#000000",

        "& .form-submitted": {
          backgroundColor: "#ffffff",
          display: "flex",
          flexDirection: "column",
          height: "100%",
          paddingTop: "20px",
          position: "absolute",
          zIndex: 9,

          "& .MuiDialogContent-root": {
            "& h4": {
              fontWeight: 500,
              fontSize: "20px",
              lineHeight: "28px",
              marginBottom: "45px",
            },

            "& .MuiButton-containedPrimary": {
              alignItems: "center",
              backgroundColor: "#12B0EE",
              boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
              borderRadius: "8px",
              display: "flex",
              fontSize: "16px",
              fontWeight: 700,
              height: "48px",
              justifyContent: "center",
              lineHeight: "19px",
              padding: 0,
              textTransform: "capitalize",
              width: "80px",
            },
          },
        },

        "& .MuiDialogTitle-root": {
          padding: "40px 40px 20px",
          fontWeight: 500,
          fontSize: "24px",
          lineHeight: 1,
        },

        "& .MuiDialogContent-root": {
          padding: "0 40px 20px",

          "& .field-group": {
            alignItems: "center",
            backgroundColor: "#F2F3F7",
            borderRadius: "8px",
            display: "flex",
            height: "48px",
            columnGap: "16px",
            justifyContent: "start",
            maxWidth: "100%",
            padding: "0 12px",
            width: "338px",

            "&:not(:last-child)": {
              marginBottom: "20px",
            },

            "& > input, & > select": {
              backgroundColor: "transparent",
              border: "none",
              flexGrow: 1,
              outline: "none",
              height: "100%",
            },

            "& .react-tel-input": {
              flexGrow: 1,
              height: "100%",

              "& .flag-dropdown": {
                left: "0 !important",

                "& .selected-flag": {
                  padding: 0,
                },
              },

              "& .form-control": {
                backgroundColor: "transparent!important",
                display: "flex",
                height: "100%!important",
                padding: "0 0 0 45px",

                "&:focus": {
                  border: "none",
                  boxShadow: "none",
                  outline: "none",
                },
              },

              "& .country-list": {
                left: "-20px",
              },
            },

            "& .MuiInputBase-formControl": {
              height: "100%",

              "& .MuiSelect-select": {
                alignItems: "center",
                display: "flex",
                height: "100%",
                padding: 0,
              },

              "& .MuiOutlinedInput-notchedOutline": {
                border: "none",
              },
            },

            "& .MuiFormControl-root": {
              height: "100%",
              width: "100%",

              "& .MuiInput-root": {
                height: "100%",

                "& .MuiInputBase-input": {
                  height: "100%",
                  padding: 0,
                },
              },
            },
          },
        },

        "& .MuiDialogActions-root": {
          justifyContent: "start",
          padding: "0 40px 40px",

          "& .MuiButton-containedPrimary": {
            alignItems: "center",
            backgroundColor: "#12B0EE",
            boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
            borderRadius: "8px",
            columnGap: "10px",
            display: "flex",
            fontSize: "16px",
            fontWeight: 700,
            height: "48px",
            justifyContent: "center",
            lineHeight: "19px",
            padding: 0,
            textTransform: "capitalize",
            width: "180px",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
